	height: 10vh;
	align-items: center;
	justify-content: space-around;
	position: fixed;
	z-index: 2;
	background-color: #ffffff;
	transition: all 0.5s;
}

.nav a {
	text-decoration: none;
	color: #000000;
}

.nav h1 {
	font-size: 40px;
	margin-top: 0;
	margin-bottom: 0;
}

.nav-links {
	display: flex;
	flex-direction: row;
	font-size: 22px;
}

.nav-links a {
	font-family: 'Roboto Mono',
		monospace;
	margin-left: 12px;
	margin-right: 12px;
	padding-top: 3px;
	padding-bottom: 3px;
}

a.middle {
	position: relative;
}

a.middle:before {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	bottom: 0;
	left: 0;
	background-color: rgb(0, 0, 0);
	visibility: hidden;
	-webkit-transform: scaleX(0);
	        transform: scaleX(0);
	transition: all 0.3s ease-in-out;
}

a.middle:hover:before {
	visibility: visible;
	-webkit-transform: scaleX(1);
	        transform: scaleX(1);
}

